</template>
<script>
// @ is an alias to /src
import { reactive, ref } from "vue";
import Header from "../../components/Header/index.vue";
import Footer from "../../components/Footer/index.vue";
import Routes from "../../services/Routes";
import * as moment from "moment";
import Swal from "sweetalert2";
import AOS from "aos";

export default {
  name: "Home",
  components: {
    Header,
    Footer,
  },
  data() {
    return {
      menu: true,
      Lessons: false,
      Cursos: false,
      ID: false,
      LoadCurso: false,
      AulaAtual: false,
      labelCurso: "Iniciar Curso",
    };
  },
  methods: {
    getDescription: function (name) {
      if (name.length > 250) {
        return name.substring(0, 250) + "...";
      }
      return name;
    },
    getURL(item) {
      var a = "";
      for (var obj in item) {
        a = item[obj];
        if (a.includes("https", 0) == true) {
          return a;
        } else {
          return null;
        }
      }
    },
    goTO(url) {
      this.$router.push(url);
    },
    getCourses() {
      let data = {
        USER_ACCOUNT_ID: JSON.parse(localStorage.getItem("SELECTED_ACCOUNT_ID"))
          ? JSON.parse(localStorage.getItem("SELECTED_ACCOUNT_ID"))
          : 0,
        USER_ID: JSON.parse(localStorage.getItem("userID")),
      };
      Routes.getCourses(data)
        .then((r) => {
          let dados = r.data.DATA;
          let final = "";
          let i = 0;
          for (; i <= dados.length - 1; i++) {
            if (dados[i].COURSE_ID == this.ID) {
              final = dados[i];
            }
          }
          this.Cursos = final;
          this.LoadCurso = false;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    getLessons() {
      let data = {
        P_USER_ACCOUNT_ID: JSON.parse(
          localStorage.getItem("SELECTED_ACCOUNT_ID")
        )
          ? JSON.parse(localStorage.getItem("SELECTED_ACCOUNT_ID"))
          : 0,
        P_USER_ID: JSON.parse(localStorage.getItem("userID")),
        P_COURSE_ID: this.ID,
      };
      Routes.getLessons(data)
        .then((r) => {
          this.Lessons = r.data.DATA;
          this.nextLesson(r.data.DATA);
        })
        .catch((e) => {
          console.log(e);
        });
    },
    Action() {
      if (this.AulaAtual == false) {
        this.$router.push(
          "/lesson/" +
            this.Lessons[0].COURSE_CLASS_USER_ID +
            "/"  +  this.Lessons[0].COURSE_ID +  '/'
        );
      } else if (this.AulaAtual == "FINISHED") {
        console.log("Curso Finalizado");
      } else {
        console.log("Curso andamento");
        this.$router.push(
          "/lesson/" +
            this.AulaAtual.COURSE_CLASS_USER_ID +
            "/"  +  this.AulaAtual.COURSE_ID + '/'
        );
      }
    },
    transformName(name) {
      name = name.replace(/ ?\d\/\d?|\.\d{1,}/g,"");
      let newname = name.split(" ").join("-");
      return newname.normalize("NFD");
    },
    nextLesson(dados) {
      let final = "";
      let i = 0;
      for (; i <= dados.length - 1; i++) {
        if (dados[i].STATUS_ID === 1) {
          this.labelCurso = "Continuar Curso";
          final = dados[i];
        } else if (dados[i].STATUS_ID === 2) {
          final = "FINISHED";
          this.labelCurso = "Curso Concluído";
        }
      }

      this.AulaAtual = final;
    },
  },
  mounted() {
    this.LoadCurso = true;
    this.getLessons();
    this.getCourses();
    AOS.init();
  },

  created() {
    window.scrollTo(0, 0);
    if (this.$route.params.id.length > 0) {
      this.ID = this.$route.params.id;
    }
  },
  setup() {},
};
</script>

